import React from 'react';

import styles from './Navbar.module.scss';

import { useRouter } from 'next/router';
import Link from 'next/link';
import routes from 'helpers/routes';

import { HeaderDataModel } from 'api/header/models/HeaderModel';
import { useSelector } from 'react-redux';
import { SuisseIntl } from 'helpers/utils/fonts';
import dynamic from 'next/dynamic';
import rect from '/public/static/img/rect.svg';
import Image from 'next/image';

const NavbarContainer = dynamic(() => import('./views/NavbarContainer'));

type NavbarTypes = {
	nav: HeaderDataModel;
	menuIsOpen?: boolean;
	searchIsShow?: boolean;
	headerRef?: React.RefObject<HTMLHeadingElement>;
	closeMobileNavbar?: () => void;
};

const Navbar = ({ nav, menuIsOpen, searchIsShow, headerRef, closeMobileNavbar }: NavbarTypes) => {
	const [activeNav, setActiveNav] = React.useState<number | null>(null);
	const [isAtStart, setIsAtStart] = React.useState<boolean>(true);
	const [isAtEnd, setIsAtEnd] = React.useState<boolean>(false);

	const navRef = React.useRef<HTMLDivElement | null>(null);

	const token = useSelector((state: any) => state.userData.token);

	const router = useRouter();

	const isMobile = useSelector((state: any) => state.isMobile.isMobile);

	const onMouseLeaveHandler = () => {
		if (searchIsShow || isMobile) return;
		setActiveNav(null);
	};

	const onMouseEnterHandler = navIndex => {
		if (searchIsShow) return;
		headerRef && headerRef.current && (headerRef.current.style.zIndex = '100001');
		setActiveNav(navIndex);
	};

	const onClickHandler = (navIndex, code) => {
		if (isMobile) {
			if (code === 'shops' || code === 'special') {
				const destinationRoute = code === 'shops' ? '/shops' : routes.catalog + nav[navIndex].code;
				if (router.asPath === destinationRoute) {
					closeMobileNavbar();
				} else {
					router.push(destinationRoute);
					closeMobileNavbar();
				}
			} else {
				setActiveNav(navIndex);
			}
		} else {
			router.push(code === 'shops' ? '/shops' : routes.catalog + nav[navIndex].code);
		}
	};

	React.useEffect(() => {
		setActiveNav(isMobile && menuIsOpen ? 0 : null);
	}, [isMobile, menuIsOpen]);

	const handleScroll = e => {
		const element = e.target;

		if (element.scrollLeft <= 0) {
			setIsAtStart(true);
		} else {
			setIsAtStart(false);
		}

		if (element.scrollWidth - element.scrollLeft <= element.clientWidth) {
			setIsAtEnd(true);
		} else {
			setIsAtEnd(false);
		}
	};

	React.useEffect(() => {
		if (activeNav !== null && menuIsOpen && isMobile) {
			document.querySelector('body').style.marginRight = `${window.innerWidth - document.documentElement.clientWidth}px`;
			document.querySelector('body').style.overflowY = 'hidden';
			document.querySelector('body').style.touchAction = 'none';
		}

		return () => {
			document.querySelector('body').style.overflowY = 'scroll';
			document.querySelector('body').style.touchAction = '';
			if (!router.query['personal-area'] && isMobile) {
				document.querySelector('body').style.marginRight = '0';
			}
			if (headerRef && headerRef.current) (headerRef.current.childNodes[0] as HTMLDivElement).style.borderBottom = '';
		};
	}, [activeNav, menuIsOpen, isMobile, headerRef, router.query]);

	React.useEffect(() => {
		if (navRef && navRef.current) {
			if (navRef.current.scrollWidth > document.documentElement.scrollWidth) {
				navRef.current.style.justifyContent = 'start';
				navRef.current.style.marginLeft = '24px';
				navRef.current.style.paddingRight = '24px';
				navRef.current.style.overflowX = 'scroll';
			}
		}
	}, [navRef]);

	return (
		<>
			<nav
				ref={navRef}
				onScroll={isMobile && handleScroll}
				className={`${styles.nav} ${searchIsShow && styles.white} ${
					!isAtEnd && !isAtStart && isMobile && styles.isGradient
				}
								${isAtStart && isMobile && styles.gradientRight}
								${isAtEnd && isMobile && styles.gradientLeft}`}
			>
				{nav
					? nav.map((item, index) => {
							if (item.code === 'muzhchiny') {
								//поменяли местами SPECIAL и МАГАЗИНЫ
								return (
									<>
										<button
											key={item.name}
											onClick={() => onClickHandler(index, item.code)}
											onMouseEnter={() => onMouseEnterHandler(index)}
											onMouseLeave={onMouseLeaveHandler}
											className={`${SuisseIntl.className} ${activeNav === index ? styles.active : ''}`}
										>
											{item.name}
										</button>
										<button
											key="Магазины"
											onClick={() => onClickHandler(5, 'shops')}
											onMouseLeave={onMouseLeaveHandler}
											className={`${SuisseIntl.className} ${activeNav === 5 ? styles.active : ''}`}
										>
											Магазины
										</button>
									</>
								);
							}
							return (
								<button
									key={item.name}
									onClick={() => onClickHandler(index, item.code)}
									onMouseEnter={() => {
										item.code !== 'special' && item.code !== 'shops' && onMouseEnterHandler(index);
									}}
									onMouseLeave={onMouseLeaveHandler}
									className={`${SuisseIntl.className} ${activeNav === index ? styles.active : ''}`}
								>
									{item.name}
								</button>
							);
					  })
					: ['ЖЕНЩИНЫ', 'ДЕТИ', 'МУЖЧИНЫ', 'МАГАЗИНЫ'].map(item => {
							return (
								<button key={item} className={SuisseIntl.className}>
									{item}
								</button>
							);
					  })}
			</nav>
			{isMobile && <Image src={rect} className={styles.line} alt="-" />}
			{isMobile && (
				<div className={styles.lkAndTranslate}>
					<Link
						href={
							router.pathname +
							(token ? routes.lk : routes.signIn) +
							(Object.keys(router.query).length !== 0
								? Object.entries(router.query)
										.map(item => `&${item[0]}=${item[1]}`)
										.join('')
								: '')
						}
						shallow
					>
						<button className={SuisseIntl.className}>Личный кабинет</button>
					</Link>
					{/*<Link href={router.asPath} locale={changeTo}>
						<button className={SuisseIntl.className}>{t('header.local')}</button>
					</Link>*/}
				</div>
			)}
			{activeNav !== null && nav && (
				<NavbarContainer
					isBunnerMove
					onMouseEnter={() => onMouseEnterHandler(activeNav)}
					onMouseLeave={onMouseLeaveHandler}
					closeMobileNavbar={closeMobileNavbar}
					category={nav.map(active => active.code)[activeNav || 0]}
					navArray={{
						categories: nav[activeNav].categories,
						section: nav[activeNav].section,
						banner: nav[activeNav].banner,
					}}
				/>
			)}
		</>
	);
};

export default React.memo(Navbar);
