import HeaderLogo from 'components/Header/views/HeaderLogo';
import Image from 'next/image';
import React from 'react';

import close from '/public/static/img/close.svg';

import styles from './MobileMenuContainer.module.scss';
import Navbar from 'components/Header/views/Navbar';
import { HeaderDataModel } from 'api/header/models/HeaderModel';

type MobileMenuContainerTypes = {
	nav: HeaderDataModel;
	menuIsOpen?: boolean;
	onClose: () => void;
};

const MobileMenuContainer = ({ nav, menuIsOpen, onClose }: MobileMenuContainerTypes) => {
	return (
		<div className={styles.MobileMenuContainer}>
			<button onClick={onClose}>
				<Image
					alt="X"
					src={close}
					width={24}
					height={24}
					style={{
						maxWidth: '100%',
						height: 'auto',
					}}
				/>
			</button>
			<HeaderLogo isWhite className={styles.logo} />
			<Navbar closeMobileNavbar={onClose} nav={nav} menuIsOpen={menuIsOpen} />
		</div>
	);
};

export default React.memo(MobileMenuContainer);
