import Image from 'next/image';
import React from 'react';
import search from '/public/static/img/search.svg';
import fav from '/public/static/img/fav.svg';
import checkout from '/public/static/img/checkout.svg';
import favActive from '/public/static/img/favActive.svg';
import checkoutActive from '/public/static/img/checkoutActive.svg';

import styles from './MobileHeader.module.scss';
import HeaderLogo from '../HeaderLogo';
import Link from 'next/link';
import routes from 'helpers/routes';
import MobileMenuContainer from './views/MobileMenuContainer';
import { useRouter } from 'next/router';
import { HeaderDataModel } from 'api/header/models/HeaderModel';
import { useTranslation } from 'next-i18next';
import { SuisseIntl } from 'helpers/utils/fonts';
import dynamic from 'next/dynamic';
import { formattedCheckoutCount } from 'helpers/utils/formattedCheckoutCount';
import { useSelector } from 'react-redux';

const Search = dynamic(() => import('../Search'));

type MobileHeaderTypes = {
	nav: HeaderDataModel;
};

const MobileHeader = ({ nav }: MobileHeaderTypes) => {
	const [menuIsOpen, setMenuIsOpen] = React.useState<boolean>(false);
	const [isFavHovered, setIsFavHovered] = React.useState<boolean>(false);
	const [isCheckoutHovered, setIsCheckoutHovered] = React.useState<boolean>(false);
	const [searchIsShow, setSearchIsShow] = React.useState<boolean>(false);

	const searchRef = React.useRef<HTMLDivElement | null>(null);

	const checkoutProduct = useSelector((state: any) => state.checkoutProduct.checkoutProduct);
	const token = useSelector((state: any) => state.userData.token);

	const router = useRouter();

	const { t } = useTranslation();

	const searchIsShowHandler = () => {
		setSearchIsShow(!searchIsShow);
	};

	const favHoverHandler = () => {
		setIsFavHovered(!isFavHovered);
	};

	const checkoutHoverHandler = () => {
		setIsCheckoutHovered(!isCheckoutHovered);
	};

	const openMobileNavbar = () => {
		setMenuIsOpen(true);
	};

	const closeMobileNavbar = () => {
		setMenuIsOpen(false);
	};

	const closeSearchWithClick = (event: Event) => {
		const { target } = event;

		if (!searchRef.current?.contains(target as Node)) {
			setSearchIsShow(false);
		}
	};

	React.useEffect(() => {
		document.addEventListener('click', closeSearchWithClick, { capture: true });
		return () => document.removeEventListener('click', closeSearchWithClick);
	}, []);

	React.useEffect(() => {
		if (!menuIsOpen) {
			if (!router.query['personal-area']) {
				document.querySelector('body').style.overflow = 'hidden scroll';
				document.querySelector('body').style.marginRight = '0';
			}
		}
	}, [menuIsOpen, router]);

	return (
		<>
			{!menuIsOpen && (
				<div className={`${styles.MobileHeader} ${SuisseIntl.className}`}>
					<div className={styles.leftBtnGrp}>
						<button onClick={openMobileNavbar} className={styles.menu}>
							{t('header.mobileMenu')}
						</button>
						<div style={{ fontSize: 0 }}>
							<Image
								alt="search"
								onClick={searchIsShowHandler}
								width={24}
								height={24}
								src={search}
								priority
								style={{
									display: 'block',
									maxWidth: '100%',
									height: 'auto',
								}}
							/>
						</div>
					</div>
					<HeaderLogo />
					<div className={styles.rightBtnGrp}>
						<Link
							href={
								router.pathname +
								(token ? routes.favorites : routes.signIn) +
								(Object.keys(router.query).length !== 0
									? Object.entries(router.query)
											.map(item => `&${item[0]}=${item[1]}`)
											.join('')
									: '')
							}
							shallow
						>
							<Image
								alt="fav"
								onTouchStart={favHoverHandler}
								onTouchEnd={favHoverHandler}
								width={24}
								height={24}
								src={isFavHovered ? favActive : fav}
								priority
								style={{
									maxWidth: '100%',
									height: 'auto',
								}}
							/>
						</Link>
						<Link href={routes.checkout}>
							<Image
								alt="checkout"
								onTouchStart={checkoutHoverHandler}
								onTouchEnd={checkoutHoverHandler}
								width={24}
								height={24}
								src={isCheckoutHovered ? checkoutActive : checkout}
								priority
								style={{
									maxWidth: '100%',
									height: 'auto',
								}}
							/>
							<p className={styles.checkoutCount}>{`(${formattedCheckoutCount(checkoutProduct.length)})`}</p>
						</Link>
					</div>
					{searchIsShow && <Search searchRef={searchRef} closeSearch={searchIsShowHandler} />}
				</div>
			)}
			{menuIsOpen && nav && <MobileMenuContainer nav={nav} onClose={closeMobileNavbar} menuIsOpen={menuIsOpen} />}
		</>
	);
};

export default React.memo(MobileHeader);
