import routes from 'helpers/routes';
import { IBMPlexMono, SuisseIntl } from 'helpers/utils/fonts';
import Link from 'next/link';
import React from 'react';

import styles from './Footer.module.scss';
import { BreadcrumbsDataModel } from 'api/breadcrumbs/models/BreadcrumbsModel';
import { useAppSelector } from 'helpers/hooks/useAppSelector';

type FooterTypes = {
	withDarkBGC?: boolean;
	className?: string;
	breadcrumbs?: BreadcrumbsDataModel;
};

const Footer = ({ withDarkBGC, className }: FooterTypes) => {
	const footerContacts = useAppSelector(state => state.footer.contacts);
	const success = useAppSelector(state => state.footer.success);
	const nav = [
		{
			name: 'Контакты',
			ref: '/contacts',
		},
		{
			name: 'Оплата и доставка',
			ref: '/help-center/payment-and-delivery',
		},
		{
			name: 'Возврат',
			ref: '/help-center/return',
		},
		{
			name: 'Оптовые продажи',
			ref: '/optovye-prodazhi',
		},
		{
			name: 'Оптовые продажи дети',
			ref: '/optovye-prodazhi-deti',
		},
		{
			name: 'ВКОНТАКТЕ',
			ref: 'https://vk.com/totti_store',
		},
		{
			name: 'TELEGRAM',
			ref: 'https://t.me/tottishop',
		},
	];
	return (
		<footer
			className={`${styles.footerContainer} ${withDarkBGC ? styles.withDarkBGC : ''} ${className ? className : ''} ${
				SuisseIntl.className
			}`}
		>
			<div className={styles.footer}>
				<div className={styles.contacts}>
					{success && <a href={`tel:${footerContacts[0]?.value}`}>{footerContacts[0]?.value}</a>}
					<Link href={nav[0].ref}>{nav[0].name}</Link>
				</div>
				<div className={styles.links}>
					<div>
						{nav &&
							nav.slice(1, 3).map((item, index) => {
								return (
									<Link key={item.name} href={item.ref}>
										{item.name}
									</Link>
								);
							})}
					</div>
					<div>
						{nav &&
							nav.slice(3, 5).map((item, index) => {
								return (
									<Link key={item.name} href={item.ref}>
										{item.name}
									</Link>
								);
							})}
					</div>
					<div>
						{nav &&
							nav.map((item, index) => {
								if (index < 5) return;
								return (
									<a key={item.name} href={item.ref} target="_blank" rel="noreferrer">
										{item.name}
									</a>
								);
							})}
					</div>
				</div>
			</div>
			<div className={styles.footer}>
				<div className={styles.contacts}>
					<p>©2024 TOTTI</p>
				</div>
				<div className={`${styles.links} ${styles.policy}`}>
					<div>
						<Link className={IBMPlexMono.className} href={routes.agreement}>
							Пользовательское соглашение
						</Link>
					</div>
					<div>
						<Link className={IBMPlexMono.className} href={routes.policy}>
							Политика конфиденциальности
						</Link>
					</div>
				</div>
			</div>
		</footer>
	);
};

export default React.memo(Footer);
