import HeaderLogo from 'components/Header/views/HeaderLogo';
import Navbar from 'components/Header/views/Navbar';
import { SuisseIntl } from 'helpers/utils/fonts';
import React from 'react';

import styles from './Header.module.scss';
import HeaderActions from './views/HeaderActions';
import MobileHeader from './views/MobileHeader';
import { useAppSelector } from 'helpers/hooks/useAppSelector';

type HeaderTypes = {
	className?: string;
};

const Header = ({ className }: HeaderTypes) => {
	const [searchIsShow, setSearchIsShow] = React.useState<boolean>(false);
	const nav = useAppSelector(state => state.header.header);

	const headerRef = React.useRef<HTMLDivElement | null>(null);
	const searchRef = React.useRef<HTMLDivElement | null>(null);
	const searchCloseButtonRef = React.useRef<HTMLImageElement | null>(null);

	const searchIsShowHandler = () => {
		setSearchIsShow(!searchIsShow);
	};

	const closeSearchWithClick = (event: Event) => {
		const { target } = event;

		if (!searchRef.current?.contains(target as Node)) {
			setSearchIsShow(false);
		}
	};

	React.useEffect(() => {
		document.addEventListener('click', closeSearchWithClick, { capture: true });
		return () => document.removeEventListener('click', closeSearchWithClick);
	}, []);

	return (
		<>
			<header ref={headerRef} className={`${styles.headerContainer} ${className ? className : ''} ${SuisseIntl.className}`}>
				<div className={`${styles.header} ${searchIsShow ? styles.white : ''}`}>
					<Navbar nav={nav && nav} headerRef={headerRef} searchIsShow={searchIsShow} />
					<HeaderLogo isWhite={searchIsShow} />
					<HeaderActions
						searchRef={searchRef}
						searchCloseButtonRef={searchCloseButtonRef}
						searchIsShow={searchIsShow}
						searchIsShowHandler={searchIsShowHandler}
					/>
				</div>
				<div className={styles.mobileHeader}>
					<MobileHeader nav={nav && nav} />
				</div>
			</header>
		</>
	);
};

export default React.memo(Header);
