import routes from 'helpers/routes';
import Image from 'next/image';
import Link from 'next/link';
import React from 'react';
import { useTranslation } from 'next-i18next';

import styles from './HeaderActions.module.scss';

import checkout from '/public/static/img/checkout.svg';
import favActive from '/public/static/img/favActive.svg';
import checkoutActive from '/public/static/img/checkoutActive.svg';
import fav from '/public/static/img/fav.svg';
import personArea from '/public/static/img/personArea.svg';
import search from '/public/static/img/search.svg';
import checkoutWhite from '/public/static/img/checkoutWhite.svg';
import favWhite from '/public/static/img/favWhite.svg';
import personAreaWhite from '/public/static/img/personAreaWhite.svg';
import searchWhite from '/public/static/img/searchWhite.svg';
import { useRouter } from 'next/router';
import { useSelector } from 'react-redux';
import dynamic from 'next/dynamic';
import { formattedCheckoutCount } from 'helpers/utils/formattedCheckoutCount';

const Search = dynamic(() => import('../Search'));

type HeaderActionsTypes = {
	searchIsShow: boolean;
	searchRef: React.RefObject<HTMLDivElement>;
	searchCloseButtonRef: React.RefObject<HTMLDivElement>;
	searchIsShowHandler: () => void;
};

const HeaderActions = ({ searchIsShow, searchRef, searchCloseButtonRef, searchIsShowHandler }: HeaderActionsTypes) => {
	const { t } = useTranslation();

	const router = useRouter();

	const token = useSelector((state: any) => state.userData.token);
	const checkoutProduct = useSelector((state: any) => state.checkoutProduct.checkoutProduct);

	const [isFavHovered, setIsFavHovered] = React.useState<boolean>(false);
	const [isCheckoutHovered, setIsCheckoutHovered] = React.useState<boolean>(false);

	const favHoverHandler = () => {
		setIsFavHovered(!isFavHovered);
	};

	const checkoutHoverHandler = () => {
		setIsCheckoutHovered(!isCheckoutHovered);
	};

	const changeTo = router.locale === 'en' ? 'ru' : 'en';

	return (
		<div className={styles.headerActions}>
			{/*<Link href={router.asPath} locale={changeTo}>
				<button className={searchIsShow ? styles.white : ''}>{t('header.local')}</button>
			</Link>*/}
			<div className={styles.iconGroup}>
				<div onClick={searchIsShowHandler} className={styles.search}>
					<Image
						alt="search"
						width={24}
						height={24}
						src={searchIsShow ? searchWhite : search}
						priority
						style={{
							maxWidth: '100%',
							height: 'auto',
						}}
					/>
				</div>
				<Link
					href={
						router.pathname +
						(token ? routes.lk : routes.signIn) +
						(Object.keys(router.query).length !== 0
							? Object.entries(router.query)
									.map(item => `&${item[0]}=${item[1]}`)
									.join('')
							: '')
					}
					shallow
				>
					<Image
						alt="personArea"
						width={24}
						height={24}
						src={searchIsShow ? personAreaWhite : personArea}
						priority
						style={{
							maxWidth: '100%',
							height: 'auto',
						}}
					/>
				</Link>
				<Link
					href={
						router.pathname +
						(token ? routes.favorites : routes.signIn) +
						(Object.keys(router.query).length !== 0
							? Object.entries(router.query)
									.map(item => `&${item[0]}=${item[1]}`)
									.join('')
							: '')
					}
					shallow
				>
					<Image
						alt="fav"
						onMouseEnter={favHoverHandler}
						onMouseLeave={favHoverHandler}
						width={24}
						height={24}
						src={searchIsShow ? favWhite : isFavHovered ? favActive : fav}
						priority
						style={{
							maxWidth: '100%',
							height: 'auto',
						}}
					/>
				</Link>
				<Link href={routes.checkout}>
					<Image
						alt="checkout"
						onMouseEnter={checkoutHoverHandler}
						onMouseLeave={checkoutHoverHandler}
						width={24}
						height={24}
						src={searchIsShow ? checkoutWhite : isCheckoutHovered ? checkoutActive : checkout}
						priority
						style={{
							maxWidth: '100%',
							height: 'auto',
						}}
					/>
					<p className={`${styles.checkoutCount} ${searchIsShow ? styles.white : 'false'}`}>{`(${formattedCheckoutCount(
						checkoutProduct.length
					)})`}</p>
				</Link>
			</div>
			{searchIsShow && (
				<Search searchRef={searchRef} searchCloseButtonRef={searchCloseButtonRef} closeSearch={searchIsShowHandler} />
			)}
		</div>
	);
};

export default React.memo(HeaderActions);
