import Image from 'next/image';
import React from 'react';
import headerImg from '/public/static/img/logo.svg';
import whiteLogo from 'public/static/img/whiteLogo.svg';

import styles from './HeaderLogo.module.scss';
import Link from 'next/link';
import routes from 'helpers/routes';

type HeaderLogoTypes = {
	isWhite?: boolean;
	className?: string;
};

const HeaderLogo = ({ isWhite, className }: HeaderLogoTypes) => {
	return (
		<Link className={`${styles.logo} ${className}`} href={routes.home}>
			<Image
				alt="TOTTI"
				width={88}
				height={25}
				src={isWhite ? whiteLogo : headerImg}
				priority
				style={{
					maxWidth: '100%',
					height: 'auto',
				}}
			/>
		</Link>
	);
};

export default React.memo(HeaderLogo);
